import {JumperPiece} from "./jumperPiece";

export class Bishop extends JumperPiece {
    iconWhite = 'C';
    iconBlack = 'c';
    name = 'bishop';
    directions = [[-1, -1], [-1, +1], [+1, -1], [+1, +1]];

    getDirections() {
        return this.directions;
    }

    clone() {
        const result = new Bishop();
        super.clone(result);
        return result;
    }
}