import {JumperPiece} from "./jumperPiece";

export class Rock extends JumperPiece {
    iconWhite = 'D';
    iconBlack = 'd';
    name = 'rock';

    directions = [[0, +1], [0, -1], [+1, 0], [-1, 0]];

    getDirections() {
        return this.directions;
    }

    clone() {
        const result = new Rock();
        super.clone(result);
        return result;
    }
}